	import { mapMutations } from 'vuex';
export default {
	data() {
		return {
			mobile: ''
		};
	},
	onLoad(opt) {
		if(opt.mobile){
			this.mobile = opt.mobile.substr(0, 3) + '****' + opt.mobile.substr(7);
		}
	},
	methods: {
		...mapMutations(['setLogout']),
		logOut() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '您确认要退出登录吗？',
				confirmText: '退出',
				success: function(ret) {
					if (ret.confirm) {
						uni.clearStorageSync();
						that.setLogout();
						uni.switchTab({
							url:"/pages/index/index"
						})
						uni.showToast({
							title: '安全退出！',
							icon: 'success'
						});
					}
				}
			});
		}
	}
};
